@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
/* Font import from Google Fonts */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --defaultFont: "Merriweather";
}

* {
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  display: none;
}

#root {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(/portfolio/static/media/background.55f2a440.jpg);
  background-size: cover;
  background-position: center;
}

#Navbar-container {
  height: 5%;
  width: 85%;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
}

.Navbar-selection {
  padding: 0 0 5px 0;
}

.Navbar-selection a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-family: var(--defaultFont);
  font-size: 20px;
  text-shadow: 1px 1px 0 black;
}

.Navbar-selection:hover {
  cursor: pointer;
  padding: 0;
  border-bottom: 5px solid rgb(160, 0, 0);
}

.selected {
  padding: 0;
  border-bottom: 5px solid rgb(160, 0, 0);
}

.Portcard {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
}
.Portcard-image {
  width: 100%;
  height: 200px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px;
  box-shadow: 0 0 5px black;
}

.Portcard a {
  color: white;
  text-shadow: 1px 1px black;
  font-size: 20px;
  font-family: var(--defaultFont);
  margin: 5px;
  font-weight: bold;
}

.Portcard a:hover {
  cursor: pointer;
}

#Home-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: scroll;
}

#Home-picContainer {
  width: 85%;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 2px solid black;
  display: flex;
  justify-content: center;
}

#Home-picture {
  height: 275px;
  width: 275px;
  background-image: url(/portfolio/static/media/me.c8854588.jpg);
  background-size: cover;
  background-position: center;
  border: 2px solid white;
  border-radius: 25px;
  box-shadow: 0 0 6px black;
}

#Home-aboutMe {
  width: 80%;
  text-align: center;
  font-family: var(--defaultFont);
  font-size: 18px;
}

.Resume-tabSpace {
  height: 100%;
  width: 100%;
}

.Resume-tabSpace ul {
  margin: 10px;
}

#Resume-container {
  width: 90%;
  height: 95%;
  margin: 20px 0 20px 0;
  overflow: scroll;
}

.sectionHeader {
  text-decoration: underline;
  margin: 0;
}

.buttonContainer {
  width: 100%;
  margin: 5px;
  display: flex;
  justify-content: center;
}

#reduxLogo {
  width: 100%;
  height: 100%;
  background-image: url(/portfolio/static/media/redux.cdb6953a.png);
  background-size: contain;
  background-position: center;
}

h4 {
  margin: 3px;
}

#Portfolio-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 20px 0 20px 0;
}

.Portfolio-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Portfolio-header h2 {
  margin: 5px;
  font-family: var(--defaultFont);
  color: maroon;
  text-decoration: underline;
}

.Portfolio-header h2:hover {
  cursor: default;
}

.Portfolio-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 80%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 10px 0 10px 0;
}

#Contact-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 1px 1px 0 black;
  overflow: scroll;
}

#Contact-phone {
  font-family: var(--defaultFont);
  font-size: 25px;
}

#Contact-social {
  width: 50%;
  display: flex;
  justify-content: center;
  border-bottom: 2px solid white;
  padding-bottom: 10px;
}

#Contact-email {
  font-family: var(--defaultFont);
  font-size: 25px;
}

#Contact-email a {
  color: white;
  text-decoration: underline;
}

#Contact-email a:hover {
  color: maroon;
  text-decoration: underline;
}

.socialIcon {
  color: white;
}

.socialIcon:hover {
  color: maroon;
}

#Contact-container p {
  margin: 10px;
  padding: 0;
  text-align: center;
}

#NotFound-container {
  text-align: center;
}

#App-container {
  width: 85%;
  height: 100%;
  margin: 0 0 5px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: rgba(255, 255, 255, 0.664);
  border-radius: 25px;
  overflow: scroll;
}

h1 {
  margin: 10px 0 5px 0;
  padding: 0;
  font-size: 35px;
  font-family: var(--defaultFont);
  text-decoration: underline;
  text-shadow: 1px 1px 0 black;
  color: rgb(255, 255, 255);
}

